
export default {
	onLaunch: function(e) {
		if (e.query.pid) {
			let pid = e.query.pid;
			uni.setStorageSync('ppid', pid);
		}
	},
	onShow: function() {
		wx.getSetting({
			success(res) {
				if (!res.authSetting['scope.userInfo']) {
					uni.reLaunch({
						url: '/pages/authorise/authorise'
					});
				}
			}
		});
	},
	onHide: function() {}
};
